import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, AbstractControlOptions, FormGroup } from '@angular/forms';

import { SignupService } from "../../services/signup/signup.service";
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { ErrorService } from 'src/app/services/errors/error.service';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  constructor(
    private fb: FormBuilder, private signupService: SignupService,
    private spinner: NgxSpinnerService, private errorService: ErrorService
  ) { }

  // Variables
  successMsg: string;
  errorMsg: string = '';

  signupForm = this.fb.group({
    email: ['', [Validators.required, Validators.email]],
    name: ['', [Validators.required]],
    password: ['', [Validators.required]],
    repeatPassword: ['', [Validators.required]],
    termsCheckbox: ['', [Validators.requiredTrue]]
  }, {
    validators: this.repeatPasswordValidator('password', 'repeatPassword')
  });

  ngOnInit(): void {
  }

  repeatPasswordValidator(controlName: string, matchingControlName: string) {
    console.log('check');

    return (formGroup: FormGroup) => {
      let control = formGroup.controls[controlName];
      let matchingControl = formGroup.controls[matchingControlName]
      if (matchingControl.errors && !matchingControl.errors.repeatPasswordValidator) {
        return;
      }
      if (control.value !== matchingControl.value) {
        matchingControl.setErrors({ repeatPasswordValidator: true });
      } else {
        matchingControl.setErrors(null);
      }
    };
  }

  clearMessage() {
    this.errorMsg = '';
    this.successMsg = '';
  }
  signup() {
    console.log('signup works', this.signupForm.value);
    this.clearMessage();
    this.spinner.show();

    const signupObj = {
      active: true,
      email: this.signupForm.get('email').value,
      name: this.signupForm.get('name').value,
      password: this.signupForm.get('password').value,
      publisher: true
    }

    console.log('signup obj', signupObj);

    this.signupService.signup(signupObj).subscribe((res: any) => {
      console.log('signup res', res);
      if (res.status === true) {
        this.spinner.hide();
        this.signupForm.reset();
        this.successMsg = res.message || 'Check Email For Login Details.';
      }else {
        this.spinner.hide();
        this.errorMsg = res.message;
      }
    }, (httpError)=> {
      console.log('error', httpError);
      
      this.spinner.hide();
      this.errorMsg = httpError.error.message || this.errorService.errorMsg;

    });

  }

}